.button {
  position: fixed;
  bottom: 2em;
  right: 2em;
  padding: 8px 8px;
  opacity: 1;
  animation: fadeIn 0.5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media (min-width: 992px) {
  .button {
    padding: 10px 10px;
    font-size: 1.3em;
  }
}